// App.jsx

import React, {useState} from 'react';
import Header from './components/Header';
import NavBar from './components/NavBar';
import Footer from './components/Footer';
import MapDisplay from './components/MapDisplay';
import SidePanel from './components/SidePanel';

import './App.css';

const App = () => {
  const [mapVisible, setMapVisible] = useState(false);
  const [sidePanelVisible, setSidePanelVisible] = useState(false);
  const [uploadedGeoJson, setUploadedGeoJson] = useState(null); // State to hold uploaded GeoJSON data

  
  const handleMapClick = () => {
    setMapVisible(!mapVisible);
    //close side panel when map is clicked
    console.log('Map Visibility:', mapVisible); 
  };

    const handleAnalysisClick = () => {
    // Toggle the visibility of the side panel
      setSidePanelVisible(!sidePanelVisible);
      console.log('After state update:', sidePanelVisible);
    };
  
    const handleFileUpload = (geoJsonData) => {
      setUploadedGeoJson(geoJsonData);
    };
 console.log('Rendering App component...');
  return (
    <div style={{display:"flex", flexDirection:"column", minHeight: '100vh'}}>
      
      <Header />
      <NavBar onMapClick={handleMapClick} onAnalysisClick={handleAnalysisClick} onFileUpload={handleFileUpload} />

      <div className="content-container">
        <div className={`map-container ${sidePanelVisible ? 'with-panel' : ''}`}>
          {mapVisible && <MapDisplay geoJsonData={uploadedGeoJson} onClose={() => setMapVisible(false)} />}
        </div>
        {sidePanelVisible && <SidePanel onClose={() => setSidePanelVisible(false)} />}
      </div>

      <div style={{ flex :1}}>
        
        
      </div>
    
      
      <Footer />
    </div>
  );
};

export default App;
